import React, { useEffect, useState } from "react";
import axios from "axios";
import Light from "./Light";

export default function Group() {

  const [config, setConfig] = useState({ lights: {} });

  const [lightState, setLightState] = useState(false);

  useEffect(() => {
    axios
      .get("http://localhost/api/2bc557f6899a11ebbe303a2125f6810c")
      .then((fetchedData) => {
        setConfig(fetchedData.data);
      });
  }, [lightState]);

  const switchLight = (state) => {
    console.log(`Current State is: ${state}`, `Switchting to ${!state}`);
    setLightState(!state);
  };

  const [showColorPicker, setShowColorPicker] = useState(false);
  const [showSceneView, setShowSceneView] = useState(false);
  const [showLightControl, setShowLightControl] = useState(false);

  return (
  <div class="content">
    <div class="cardGrid">
      {Object.entries(config.lights).map((light) => (
        <li key={light}>
          <Light
            lightState={lightState}
            setLightState={setLightState}
            light={light}
          />
        </li>
      ))}
      </div>
    </div>
  );
}
